* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}
a {
   list-style: none;
   text-decoration: none;
}
#header {
   height: 76px;
   background-image: linear-gradient(to right ,#E51D29,#273393);
   position: fixed;
   left:  0;
   top: 0;
   right: 0;
   z-index: 0;
}
.header-container{
   width: 1300px;
   margin: 0 auto;
   display: flex;
}
.image-nav{
  float: left;
  line-height: 76px;
}
.image-nav img{
  margin:  0;
  padding: 0;
  max-height: 40px;
  vertical-align: middle;

}
.image-nav span{
  color:  #fff;
  padding-left: 4px;
  text-shadow: 1px 1px #aab2c7;
  font-weight: 600;
}
.nav {
  float: left;
   margin-left: 30px;
   
}
.nav > li {
   display: inline-block;
   padding-right: 46px;
}
.nav a:hover{
  color: #aab2c7;
}
.nav li i {
  color: #9399db;
  padding-right: 4px;
}
.nav a {
  color:  #fff;
}
.nav-item{
  padding: 2px 20px 0px 20px;
}
.nav-item-search{
  padding: 5px 20px 0px 20px;
}
.nav-icon{
  width: 20px;
  height: auto;
}
#header .search-btn {
 margin: 0px 16px;
 position: relative;

}
.search-btn .search-icon {
  position: absolute;
  top: 4px;
  right: -13%;
  color:  #fff;

}
.search-btn .search-icon:hover {
  color: #aab2c7;
  cursor: pointer;
}
.search-btn input {
  border: 2px solid #9399db ;
  border-radius: 4px;
  width: 350px;
  padding: 5px;
}
.search_icon{
  padding-top: 1px;
  padding-right: 20px;
}
#header .nav-form {
  float: right;
  line-height: 4;
}

*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
.tabs{
    display:flex;
    width: 1200px;
    margin: 0 auto;
}
#nav-tabs1 , #nav-tabs2  {
    width: 200px;
    height: 50px;
    text-decoration: none;
    color: #fff;
    border: none;
    background-color: rgb(255,0,68);
    border-radius: 4px;
    box-shadow: inset 0 0 0 0 #f9e506;
    transition: ease-out 0.3s;
    font-size: 1rem;
    outline: none;
    flex: left 1;
    margin-left: 20px;
} 
#nav-tabs1:hover {
    box-shadow: inset 300px 0 0 0 #f9e506;
    cursor: pointer;
    color: #000;
}
#nav-tabs2:hover {
    box-shadow: inset 300px 0 0 0 #f9e506;
    cursor: pointer;
    color: #000;
}




*{
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}
#ourserv article h1, #latestp article h1{
	font-family: LeagueGothic, sans-serif;
	font-weight: normal;
	text-transform: uppercase;
	font-size: 18px;
	margin-bottom: 5px;
}
#ourserv img, #latestp img{
	margin-bottom: 5px;
    box-shadow: 0 4px 6px #000;
}
#latestp article{
	width: 300px;
	display: inline-block;
	*display: inline;
	zoom:1;
    padding: 20px;
}
.img-news{
    width: 100%;
    height: auto;
    transition: -webkit-transform .2s;
    transition: transform .2s;
    transition: transform .2s, -webkit-transform .2s;
}
.img-news:hover { /* IE 9 */
    -webkit-transform: scale(1.1); /* Safari 3-8 */
    transform: scale(1.1); 
}
.tabs{
	margin-left:20px;
	height: 45px;
	border-left: 5px solid #2E3192;
}
.tabs-item{
	width: 100%;
	height: 80px;
	padding: 20px;
	display: flex;
}
.tabs-item1{
	width: 350px;
	height: 80px;
	background-color: rgba(237, 28, 36, 0.1);
	flex: left 1;
}
.tabs-item2{
	width: 350px;
	height: 80px;
	background-color: rgba(40, 167, 69, 0.1);
	flex: left 1;
	margin-left: 55px;
}
.tabs-item3{
	width: 350px;
	height: 80px;
	background-color: #F0EFF4;
	flex: left 1;
	margin-left: 55px;
}
.tabs-item1 p{
	font-weight: 800;
    font-size: 22px;
    line-height: 22px;
    color: #ED1C24;
    font-family: 'Merriweather';;
}
.tabs-item2 p{
	font-weight: 800;
    font-size: 22px;
    line-height: 22px;
    color: #28A745;
    font-family: 'Merriweather';;
}
.tabs-item3 p{
	font-weight: 800;
    font-size: 22px;
    line-height: 22px;
    color: #333333;
    font-family: 'Merriweather';;
}
.tabs-item1 h6, .tabs-item2 h6 , .tabs-item3 h6{
